.canvasDiv{width:500px;height:500px}.myCanvas{border:3px solid #ccc;background:red}.testerClass{width:300px;height:300px;background:red}body{text-align:center;padding:50px}.scene{display:inline-block;margin-top:50px;width:150px;height:150px;perspective:600px}.cube{position:relative;width:inherit;height:inherit;transform-style:preserve-3d;transition:transform 0.6s}.cube-face{width:inherit;height:inherit;position:absolute;background:#8d8989;opacity:0.8}.cube-face-front{background:yellow;transform:translate3d(0, 0, 75px)}.cube-face-back{background:black;transform:rotateY(180deg) translate3d(0, 0, 75px)}.cube-face-left{background:green;transform:rotateY(-90deg) translate3d(0, 0, 75px)}.cube-face-right{background:magenta;transform:rotateY(90deg) translate3d(0, 0, 75px)}.cube-face-top{background:blue;transform:rotateX(90deg) translate3d(0, 0, 75px)}.cube-face-bottom{background:red;transform:rotateX(-90deg) translate3d(0, 0, 75px)}#radio-back:checked ~ .scene .cube{transform:rotateY(180deg)}#radio-left:checked ~ .scene .cube{transform:rotateY(90deg)}#radio-right:checked ~ .scene .cube{transform:rotateY(-90deg)}#radio-top:checked ~ .scene .cube{transform:rotateX(-90deg)}#radio-bottom:checked ~ .scene .cube{transform:rotateX(90deg)}.largeSeperator{height:800px}.cog-header{width:600px;height:300px;display:inline-block;-webkit-animation-timing-function:ease-out;animation-timing-function:ease-out;-webkit-animation:10s linear infinite changeColor;animation:10s linear infinite changeColor}.cog-header h2{margin:1em auto;text-align:center;margin-top:20%}.cog-container{z-index:-1;position:relative;width:300px;height:300px;-webkit-animation:1s linear infinite rotateCog;animation:1s linear infinite rotateCog}.cog-container img{width:300px;height:300px}.cog-container-fast{z-index:-1;position:relative;width:300px;height:300px;-webkit-animation:1s linear infinite rotateCog;animation:1s linear infinite rotateCog}.cog-container-fast img{width:300px;height:300px}.rotate-group{position:relative;width:250px;height:360px}.rotate-group img{width:100%;height:100%;border-radius:20px}.rotate-group:hover .rotate-x{-webkit-animation:animate-x 5s infinite linear;animation:animate-x 5s infinite linear}.rotate-x{perspective:300px;-webkit-animation:animate-x 5s infinite linear;animation:animate-x 5s infinite linear}@-webkit-keyframes animate-x{0%{-webkit-transform:rotateX(0)}100%{-webkit-transform:rotateX(360deg)}}@keyframes animate-x{0%{transform:rotateX(0)}100%{transform:rotateX(360deg)}}@-webkit-keyframes rotateCog{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes rotateCog{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@-webkit-keyframes changeColor{0%{background:blue}50%{background:red}70%{background:green}100%{background:blue}}@keyframes changeColor{0%{background:blue}50%{background:red}70%{background:green}100%{background:blue}}
